import React from "react"
import { Text, TextWrapper } from "../components/shared/TextWrapper"
import { SkillWrapper, Items, SkillItem } from "../components/shared/Skill"
import { useStaticQuery, graphql } from "gatsby"

const About = () => {
  const data = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          stacks
        }
      }
    }
  `)
  return (
    <>
      <TextWrapper>
        <Text>
          Welcome to my website! My name is Rory Donald and I'm currently a student at The University Of Utah's school of Engineering.
          Im expected to graduate by 2022 with a degree in Computer Science. I'd make this website dark theme'd but I like to go on long hikes and watch my eyesight deteriorate.
        </Text>
      </TextWrapper>
      <SkillWrapper>
        <h4>Development Skills:</h4>
        <Items>
          {data.site.siteMetadata.stacks.map(stack => (
            <SkillItem key={stack}>{stack}</SkillItem>
          ))}
        </Items>
      </SkillWrapper>
    </>
  )
}
export default About
